.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  justify-content: center; 
}
.exp-designition{
  font-size: 17px; 
  font-weight: 500; 
  font-family: 'Sora', sans-serif;
}
.exp-organization{
  font-size: 15px; 
  font-weight: 400; 
  font-family: 'Sora', sans-serif;
}
.exp-description{
  font-size: 12px !important; 
  font-weight: 400 !important; 
  font-family: 'Sora', sans-serif !important;
}
.experience {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 40px;
  grid-column-gap: 15%;
  width: 85%;
}

.experience.odd-count {
  grid-template-columns: 1fr 1fr; 
}

.experience.odd-count .experience-element:last-child {
  grid-column: 1 / -1; 
  justify-self: center; 
  width: 42.5%;
}

.experience-element {
  display: flex; 
  align-items: center; 
  position: relative;
  padding: 10px;
  transition: transform 0.3s ease, background-color 0.3s ease;
  backdrop-filter: blur(10px);
}

.experience-content {
  margin-left: 50px; 
  flex: 1; 
}

.year {
  position: absolute; 
  left: -125px; 
  top: 50%; 
  transform: translateY(-50%); 
  font-size: 60px; 
  font-weight: 700; 
  font-family: 'Sora', sans-serif;
}
.date-text {
  display: block; 
  font-size: 12px; 
  font-weight: 400;
  margin-top: 5px;
  text-align:end;
  font-family: 'Sora', sans-serif;
}
.skill-title {
  font-size: 15px; 
  font-weight: 600;
  font-family: 'Sora', sans-serif;
  overflow: hidden; 
  white-space: nowrap; 
  animation: none;
  animation-duration: 0.1s; 
  animation-fill-mode: forwards; 
}

.skill-icon {
  width: 40px; 
  height: 40px; 
  margin-bottom: 10px; 
  display: flex; 
  justify-content: center; 
  align-items: center; 
}

@keyframes typing {
  0% {
    width: 0; 
  }
  100% {
    width: 100%; 
  }
}

.skill-item {
  margin: 15px;
  text-align: center;
  transition: transform 0.3s ease; 
}


.skill-item:hover {
  transform: scale(1.1); 
}

.skill-item:hover .skill-title {
  animation: typing 0.5s steps(10) forwards; 
}

.experience-element.dark {
  background: rgba(255, 255, 255, 0.2);
}

.experience-element.light {
  background: rgba(0, 0, 0, 0.2);
}

.experience-element.dark:hover {
  transform: scale(1.05);
  background-color: rgba(255, 255, 255, 0.4);
}

.experience-element.light:hover {
  transform: scale(1.05);
  background-color: rgba(0, 0, 0, 0.4); 
}

.experience-element:hover .year {
  color: #FFBB1A; 
}

.main-heading-h1 {
  font-size: 19px;
  font-family: 'Sora', sans-serif;
  font-weight: 600;
  line-height: 26px;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px 0;
}

@media (max-width: 768px) {
  .experience {
    display: flex; 
    flex-direction: column; 
    align-items: center; 
    width: 100%; 
  }

  .experience-element {
    width: 90%; 
    margin: 10px 0; 
    flex-direction: column; 
    align-items: flex-start;
    padding: 20px; 
  }

  .experience.odd-count .experience-element:last-child {
    width: 90%; 
    justify-self: center;
  }

  .year {
    position: static; 
    font-size: 40px; 
    margin-bottom: 10px; 
  }
}

